import React, {useContext, useRef, useState} from "react";
import "./StartupProjects.scss";
import {bigProjects} from "../../portfolio";
import {Fade} from "react-reveal";
import StyleContext from "../../contexts/StyleContext";
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';


export default function StartupProject() {
  const projectCardRef = React.useRef(null);
  const [scrollPosition, setScrollPosition] = useState(0);

  const scrollContainer = useRef(null);

  const handleRightArrowClick = () => {
    const container = scrollContainer.current;
    const cardWidth = projectCardRef.current?.offsetWidth;
    console.log(cardWidth);
    if (container && cardWidth) {
      const newScrollPosition = Math.min(
        scrollPosition + cardWidth,
        container.scrollWidth - container.offsetWidth
      );
      container.scrollLeft = newScrollPosition;
      setScrollPosition(newScrollPosition);
    }
  };
  
  const handleLeftArrowClick = () => {
    const container = scrollContainer.current;
    const cardWidth = projectCardRef.current?.offsetWidth;
    if (container && cardWidth) {
      const newScrollPosition = Math.max(
        scrollPosition - cardWidth,
        0
      );
      container.scrollLeft = newScrollPosition;
      setScrollPosition(newScrollPosition);
    }
  };
  


  function openUrlInNewTab(url) {
    if (!url) {
      return;
    }
    var win = window.open(url, "_blank");
    win.focus();
  }

  const {isDark} = useContext(StyleContext);
  if (!bigProjects.display) {
    return null;
  }
  
  return (
    <Fade bottom duration={1000} distance="20px">
      <div className="main" id="projects">
        <div>
          <h1 className="skills-heading">{bigProjects.title}</h1>
          <p
            className={
              isDark
                ? "dark-mode project-subtitle"
                : "subTitle project-subtitle"
            }
          >
            {bigProjects.subtitle}
          </p>
          
          <div className="arrows-container">
            <div className="arrow-button left" onClick={handleLeftArrowClick}>
              <ChevronLeftIcon className="arrow-icon" fontSize="large" />
            </div>
            <div className="arrow-button right" onClick={handleRightArrowClick}>
              <ChevronRightIcon className="arrow-icon" fontSize="large" />
            </div>
          </div>



          <div ref={scrollContainer} className="projects-container">
            {bigProjects.projects.map((project, i) => {
              return (
                <div
                  ref={projectCardRef}
                  key={i}
                  className={
                    isDark
                      ? "dark-mode project-card project-card-dark"
                      : "project-card project-card-light"
                  }
                >
                  {project.image ? (
                    <div
                      className="project-image"
                      style={{ backgroundImage: `url(${project.image})` }}
                    ></div>
                  ) : null}
                  <div className="project-detail">
                    <h5
                      className={isDark ? "dark-mode card-title" : "card-title"}
                    >
                      {project.projectName}
                    </h5>
                    <p
                      className={
                        isDark ? "dark-mode card-subtitle" : "card-subtitle"
                      }
                    >
                      {project.projectDesc}
                    </p>
                    {project.footerLink ? (
                      <div className="project-card-footer">
                        {project.footerLink.map((link, i) => {
                          return (
                            <span
                              key={i}
                              className={
                                isDark ? "dark-mode project-tag" : "project-tag"
                              }
                              onClick={() => openUrlInNewTab(link.url)}
                            >
                              {link.name}
                            </span>
                          );
                        })}
                      </div>
                    ) : null}
                    {project.skills ? (
                      <div className="project-card-footer">
                        {project.skills.map((skill, i) => {
                          return (
                            <span
                              key={i}
                              className="skill-tag"
                            >
                              {skill}
                            </span>
                          );
                        })}
                      </div>
                    ) : null}

                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </Fade>
  );
}
